.Home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh; /* Set the height of the container to 100% of the viewport height */
  }
  
  .centered-image {
    /* Define styles to center the image */
    max-width: 100%;
  }
  /* Light mode styles */
.light {
    background-color: #f65a00;
    color: #0eefff;
  }
  
  /* Dark mode styles */
  .dark {
    background-color: #333;
    color: #f0f0f0;
  }
  