

.logo {
    color: rgb(47, 11, 226);
    font-size: 30px;
}
.pages ul li{
    font-size: 20px;
    justify-content: center;
}
nav {
    background-color: #bd55a3;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 10px;
  }
  
  li {
    display: inline;
    margin-right: 20px;
  }
  
  li:last-child {
    margin-right: 0;
  }
  
  a {
    color: #180505;
    text-decoration: none;
    padding: 6px;
  }
  
  a:hover {
    color: #756d77;
  }
